<template>
  <view>
    <view class="missonbg" v-for="(item,index) in auditlist" :key='index' @click="gettodetail(item.task_id,item.id)">
      <view class="mBgbottom">
        <image :src="item.avatar" mode="scaleToFill" style="width: 48rpx;
                height: 48rpx;vertical-align: top;margin-right: 10rpx;border-radius: 50%;"></image>
        <text>{{item.task_name}}任务已完成请审核</text>
      </view>
      <view style="display: flex; justify-content: flex-end;margin-top: 52rpx;">
        <view class="btnpos" style="margin-right: 20rpx;" @click.stop="confirmbtn(item.id)">同意</view>
        <view class="btnpos" @click.stop="open(item.id)">拒绝</view>
      </view>
    </view>

			<uni-popup ref="inputDialog" type="dialog">
				<uni-popup-dialog ref="inputClose" mode="input" title="拒绝原因" value="" :beforeClose="true" placeholder="请输入拒绝原因" @confirm="dialogInputConfirm" @close='close'></uni-popup-dialog>
			</uni-popup>
  </view>
</template>

<script>
  var http = require("../../utils/http.js");

  export default {
    data() {
      return {
        auditlist: "",
        maskdetail:'',
        id:'',
      }
    },
    onLoad() {
      this.getaudit()
    },
    methods: {
      //查看任务详情
      gettodetail(id,subid){
        console.log(id)
        uni.navigateTo({
          url: '/pages/Mission/Mission?id=' + id+'&subid='+subid+'&type='+3
        })
      },
      //获取任务审核列表
      // 2审核中,3已完成,4拒绝
      getaudit() {
        const that = this;
        var url = 'api/team/auditTaskList';
        let data = {
          type:2,
          openid: uni.getStorageSync('openid')
        }
        http.postReq(url, data, function(res) {
          console.log('321', res)
          if (res.code == 200) {
            that.auditlist = res.data
          } else {
            uni.showToast({
              title: res.msg,
              icon: 'none',
              duration: 2000
            });
          }
        });
      },
      /* 审核任务同意与拒绝*/
      open(id) {
        this.id=id
        this.$refs.inputDialog.open()
      },
      close(){
         this.$refs.inputDialog.close()
      },
			dialogInputConfirm(val) {
        console.log(val)
        this.maskdetail = val
       this.close()
        this.submit(2,this.id)
			},
      confirmbtn(id){
        this.submit(1,id)
      },
      submit(is, id) {
        const that = this;
        var url = 'api/team/auditTask';
        let data = {
          openid: uni.getStorageSync('openid'),
          id,
          is,
          mask:that.maskdetail
        }
        http.postReq(url, data, function(res) {
          if (res.code == 200) {
            uni.showToast({
              title: res.msg,
              icon: 'none',
              duration: 2000
            });
            that.getaudit()
          } else {
            uni.showToast({
              title: res.msg,
              icon: 'none',
              duration: 2000
            });
          }
        });
      },
    }
  }
</script>

<style>
  page {
    background-color: #f4f6ed;
  }

  /* 最后结束的表 */
  .missonbg {
    box-sizing: border-box;
    width: 690rpx;
    background: url(../../static/myteambg1.png) no-repeat;
    background-size: 100% 100%;
    padding: 40rpx 20rpx 0;
    position: relative;
    margin-top: 20rpx;
    margin-left: 30rpx;
  }

  .mBgbottom {
    font-size: 28rpx;
    font-family: PingFang SC, PingFang SC-Regular;
    line-height: 48rpx;
  }

  .btnpos {
    width: 136rpx;
    height: 60rpx;
    background: #d0d6f8;
    border-radius: 30rpx;
    line-height: 60rpx;
    text-align: center;
    opacity: 1;
    font-size: 28rpx;
    font-family: PingFang SC, PingFang SC-Regular;
    color: #8913ff;
    margin-bottom: 20rpx;
  }
</style>
